<template>
  <v-app>
    <v-main>
      <v-container>
        <v-row>
          <v-col id="col_geral">
            <v-card
              id="main_card"
              class="mx-auto"
              color="black"
              dark
            >
              <v-card-title>
                 <h3 id="title_card" >Login</h3>
              </v-card-title>
                <v-form
                  ref="form"
                  v-model="valid"
                  lazy-validation
                >
                <v-card-text class="text-h5 font-weight-bold">
                  
                    <v-text-field
                      label="Nome de usuário"
                      v-model="usuario.nome"
                      :rules="[rulesUser.required, rulesUser.min]"
                    />
                    <v-text-field
                      label="Senha"
                      :rules="[rulesPass.required, rulesPass.min]"
                      :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'"
                      :type="show ? 'text' : 'password'"
                      v-model="usuario.senha"
                      @click:append="show = !show"
                    />
                </v-card-text>

              <v-card-actions id="actions_card">
                <v-btn
                  depressed
                  color="teal"
                  block
                  outlined
                  @click="logar"
                >
                  Logar
                </v-btn>
              </v-card-actions>
              </v-form>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
import {login} from '../service/Axios'


export default {
  name: 'App',

  data: () => ({
    show: false,
    valid: true,
    usuario: {
      nome: '',
      senha: ''
    },
    rulesPass: {
      required: value => !!value || 'Campo obrigatório.',
      min: v => v.length >= 6 || 'Senha curta',
      emailMatch: () => (`Senha inválida`),
    },
    rulesUser: {
      required: value => !!value || 'Campo obrigatório.',
      min: v => v.length >= 4 || 'Nome de usuário curto',
      emailMatch: () => (`Usuário inválido`),
    },
  }),
  methods: {
    async logar() {
      this.$refs.form.validate()
      if(this.$refs.form.validate()) {
        try {
          var info = await login(this.usuario)
          if(info.code === 200) {
            this.$router.push('/home')
          }
        } catch (error) {
          // console.log(error)
          alert("[ERRO] - Usuário ou senha incorretos")
        } 
      }
    }
  }
};
</script>

<style>
  v-card {
    height: 80%;
  }
  #main_card {
    margin-top: 17%;
    width: 25rem;
    height: auto; 
    background: linear-gradient(180deg, #E3F3F5 0%, rgba(227, 243, 245, 0.53125) 34.9%, rgba(227, 243, 245, 0) 100%);
    border-radius: 5px;
  }
   #title_card {
    color: #000000;
    font-size: 33px;
    margin: auto;
    justify-content: center;
  } 
  #actions_card {
    justify-content: center;
  }
</style>